
.App {
  text-align: center;
}

/*basic reset*/
* {margin: 0; padding: 0;}

html {
  height: 100%;
  background: linear-gradient(rgba(147, 18, 74, 0.4), rgba(129, 38, 191, 0.5)) no-repeat fixed;
  overflow-x: hidden;
}

body {
  font-family: montserrat, arial, verdana, serif;
  overflow-x: hidden;
}

form{
  display: grid;
  min-height: 300px;
  width: 100%;
  button{
   max-height: 3em;
  }
}
.from-step-wrapper{
  width: 100%;
  padding-top: 1em;
  form, {
    display: grid;
    width: 100%;
    place-items: stretch;
    padding: 1em 0;
    min-height: 50vh;

  }
  .nav-buttons{
    display: grid;
    grid-auto-flow: column;
    column-gap: 20px;
    align-self: end;
    align-items: end;
  }
}
@media only screen and (max-width: 400px){
  .css-rbezjk-MuiGrid-root{
    margin-top: 5% !important;
  }
  .from-step-wrapper {
    width: 100%;
    padding-top: 1em;

    form {
      min-height: 60vh;
    }
  }
}